import { Injectable } from '@angular/core';
import {
  Router,
  Resolve,
  RouterStateSnapshot,
  ActivatedRouteSnapshot,
} from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  BrandStatus,
  GetBrandDetailsGQL,
  GetBrandDetailsQuery,
} from '../../../graphql/graphql';

@Injectable()
export class BrandResolver implements Resolve<GetBrandDetailsQuery['brand']> {
  constructor(
    private getBrandDetailsGQL: GetBrandDetailsGQL,
    private router: Router,
  ) {}

  resolve(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ):
    | GetBrandDetailsQuery['brand']
    | Observable<GetBrandDetailsQuery['brand']>
    | Promise<GetBrandDetailsQuery['brand']>
    | null
    | undefined {
    const id = parseInt(route.paramMap.get('id') || '', 10);
    if (id) {
      return this.getBrandDetailsGQL
        .fetch(
          {
            where: {
              id,
            },
          },
          {
            fetchPolicy: 'network-only',
          },
        )
        .pipe(
          map((result) => {
            if (result.error || result.errors) {
              this.router.navigateByUrl('/exception/500');
              return undefined;
            }

            if (result.data?.brand) {
              return result.data.brand;
            } else {
              this.router.navigateByUrl('/exception/404');
              return undefined;
            }
          }),
        );
    } else {
      return {
        status: BrandStatus.Active,
      } as GetBrandDetailsQuery['brand'];
    }
  }
}
