import { Location } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzUploadFile } from 'ng-zorro-antd/upload';
import { of, Subject, Observable } from 'rxjs';
import { mergeMap, takeUntil } from 'rxjs/operators';
import Countries from './slim-2.json';

import { environment } from '../../../../environments/environment';
import {
  BrandCreateInput,
  BrandStatus,
  BrandType,
  BrandTypeValue,
  BrandUpdateInput,
  CreateBrandGQL,
  GetBrandDetailsQuery,
  LanguageCode,
  UpdateBrandGQL,
} from '../../../../graphql/graphql';
import { FileService } from '../../../shared/services/file/file.service';
import { StringService } from '../../../shared/services/string/string.service';

@Component({
  selector: 'app-brand-details',
  templateUrl: './brand-details.component.html',
  styleUrls: ['./brand-details.component.scss'],
})
export class BrandDetailsComponent implements OnInit, OnDestroy {
  BrandStatus = BrandStatus;
  countries = Countries;
  destroy$: Subject<boolean>;
  brand: GetBrandDetailsQuery['brand'];
  validateForm: FormGroup;
  thumbnailFile?: NzUploadFile;
  thumbnailUrl?: string;
  loading = false;

  brandTypes: Array<BrandType> = [
    {
      id: '1',
      value: BrandTypeValue.DepartmentStore,
    },
    {
      id: '2',
      value: BrandTypeValue.DrugStore,
    },
    {
      id: '3',
      value: BrandTypeValue.RoadStore,
    },
    {
      id: '4',
      value: BrandTypeValue.OnlineOther,
    },
  ];

  constructor(
    private fb: FormBuilder,
    private fileService: FileService,
    private location: Location,
    private route: ActivatedRoute,
    private stringService: StringService,
    private httpClient: HttpClient,
    private createBrandGQL: CreateBrandGQL,
    private updateBrandGQL: UpdateBrandGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.destroy$ = new Subject();
    this.validateForm = this.fb.group({
      name: [null, Validators.required],
      description: [null],
      types: [[]],
      status: [BrandStatus.Active, Validators.required],
      slug: [null, Validators.required],
      coo: [null],
    });
  }

  ngOnInit(): void {
    this.route.data.pipe(takeUntil(this.destroy$)).subscribe(({ brand }) => {
      this.brand = brand;
      this.thumbnailUrl = this.brand?.logoUrl;
      this.validateForm.patchValue({
        name: this.brand?.translations?.[0]?.name,
        description: this.brand?.translations?.[0]?.description,
        status: this.brand?.status === BrandStatus.Active ? true : false,
        slug: this.brand?.translations?.[0]?.slug,
        types: this.brand?.types,
        coo: this.brand?.coo,
      });
      if (!this.brand?.translations?.[0]?.slug) {
        this.generateSlug();
      }
    });
  }

  beforeUploadThumbnail = (file: NzUploadFile): boolean => {
    this.thumbnailFile = file;
    this.fileService.getBase64((file as unknown) as File, (img: string) => {
      this.thumbnailUrl = img;
    });
    return false;
  };

  generateSlug(): void {
    const name = this.validateForm.get('name')?.value;
    if (!name?.trim().length) {
      return;
    }
    this.validateForm.patchValue({
      slug: this.stringService.stringToSlug(name),
    });
  }

  compareBrandTypes(type1: BrandType, type2: BrandType): boolean {
    return (type1 && type2 && type1.id === type2.id) || type1 === type2;
  }

  submitForm(): void {
    Object.values(this.validateForm.controls).forEach((control) => {
      control.markAsDirty();
      control.updateValueAndValidity();
    });

    if (this.validateForm.invalid) {
      return;
    }

    this.loading = true;

    let coverObservable: Observable<{
      url: string;
    }>;
    if (this.thumbnailFile) {
      const formData = new FormData();
      formData.append('file', (this.thumbnailFile as unknown) as File);
      coverObservable = this.httpClient.post<{ url: string }>(
        `${environment.apiUri}/single-upload?useWatermark=false`,
        formData,
      );
    } else {
      coverObservable = of({
        url: this.brand?.logoUrl || '',
      });
    }

    coverObservable
      .pipe(
        takeUntil(this.destroy$),
        mergeMap(({ url }) => {
          const data: Pick<
            BrandCreateInput & BrandUpdateInput,
            'status' | 'logoUrl' | 'coo'
          > = {
            status: this.validateForm.get('status')?.value
              ? BrandStatus.Active
              : BrandStatus.Inactive,
            logoUrl: url,
            coo: this.validateForm.get('coo')?.value,
          };

          if (this.brand?.uid) {
            return this.updateBrandGQL.mutate(
              {
                where: {
                  // tslint:disable-next-line: deprecation
                  id: this.brand.id,
                },
                data: {
                  ...data,
                  types: {
                    set: this.validateForm
                      .get('types')
                      ?.value.map((type: any) => ({
                        id: type.id,
                      })),
                  },
                  translations: {
                    create:
                      (!this.brand?.translations?.[0] && [
                        {
                          name: this.validateForm.get('name')?.value,
                          description: this.validateForm.get('description')
                            ?.value,
                          isOriginal: true,
                          language: LanguageCode.Vi,
                          slug: this.validateForm.get('slug')?.value,
                        },
                      ]) ||
                      undefined,
                    set: this.brand?.translations?.[0] && [
                      {
                        id: this.brand.translations[0].id,
                        name: this.validateForm.get('name')?.value,
                        description: this.validateForm.get('description')
                          ?.value,
                        isOriginal: true,
                        language: LanguageCode.Vi,
                        slug: this.validateForm.get('slug')?.value,
                      },
                    ],
                  },
                },
              },
              {
                errorPolicy: 'all',
              },
            );
          } else {
            return this.createBrandGQL.mutate(
              {
                data: {
                  ...data,
                  types: {
                    connect: this.validateForm
                      .get('types')
                      ?.value.map((type: any) => ({
                        id: type.id,
                      })),
                  },
                  translations: {
                    create: [
                      {
                        name: this.validateForm.get('name')?.value,
                        description: this.validateForm.get('description')
                          ?.value,
                        isOriginal: true,
                        language: LanguageCode.Vi,
                        slug: this.validateForm.get('slug')?.value,
                      },
                    ],
                  },
                },
              },
              {
                errorPolicy: 'all',
              },
            );
          }
        }),
      )
      .subscribe(
        ({ errors, data }) => {
          if (errors) {
            this.msgService.error(
              this.translateService.instant(
                // tslint:disable-next-line: deprecation
                `brand.${this.brand?.id ? 'update' : 'create'}_error`,
              ),
            );
          }

          if (data) {
            this.msgService.success(
              this.translateService.instant(
                // tslint:disable-next-line: deprecation
                `brand.${this.brand?.id ? 'update' : 'create'}_success`,
              ),
            );
            this.location.back();
          }
          this.loading = false;
        },
        () => {
          this.msgService.error(
            this.translateService.instant(
              // tslint:disable-next-line: deprecation
              `brand.${this.brand?.id ? 'update' : 'create'}_error`,
            ),
          );
          this.loading = false;
        },
      );
  }

  cancel(): void {
    this.location.back();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
