import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzTableQueryParams } from 'ng-zorro-antd/table';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  BrandListFieldsFragment,
  BrandStatus,
  GetBrandListGQL,
  GetBrandListQuery,
  GetBrandListQueryVariables,
  OrderBy,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-brand-list',
  templateUrl: './brand-list.component.html',
  styleUrls: ['./brand-list.component.scss'],
})
export class BrandListComponent implements OnInit, OnDestroy {
  BrandStatus = BrandStatus;
  destroy$: Subject<boolean> = new Subject();
  brands: Array<BrandListFieldsFragment> = [];
  brandsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetBrandListQuery, GetBrandListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  searchForm: FormGroup;
  text: string | undefined;
  status: BrandStatus | undefined;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private getBrandListGQL: GetBrandListGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.searchForm = this.fb.group({
      name: [null],
      status: [null],
    });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('page-size') || '10', 10);
        this.text = paramMap.get('text') || '';
        this.status = Object.values(BrandStatus).includes(
          paramMap.get('status') as BrandStatus,
        )
          ? (paramMap.get('status') as BrandStatus)
          : undefined;
        this.searchForm.patchValue({
          name: this.text,
          status: this.status,
        });

        if (this.dataSubscription) {
          this.refetch();
        } else {
          this.dataQueryRef = this.getBrandListGQL.watch(this.getVariables(), {
            notifyOnNetworkStatusChange: true,
            fetchPolicy: 'network-only',
          });
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(({ data, loading, error, errors }) => {
              this.loading = loading;
              if (loading) {
                return;
              }

              if (error || errors) {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
                return;
              }

              if (data) {
                this.brands = data.brands;
                this.brandsCount = data.brandsConnection.aggregate.count;
              }
            });
        }
      });
  }

  refetch(): void {
    this.dataQueryRef?.refetch(this.getVariables());
  }

  private getVariables(): GetBrandListQueryVariables {
    return {
      where: {
        translationsSome: {
          nameContains: this.text || undefined,
        },
        status: this.status,
      },
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      orderBy: {
        id: OrderBy.Desc,
      },
    };
  }

  handleQueryParamsChange(params: NzTableQueryParams): void {
    this.router.navigate([], {
      queryParams: {
        page: params.pageIndex,
        'page-size': params.pageSize,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleSearch(): void {
    const searchData = this.searchForm.value;
    const status = searchData.status || undefined;
    const text = searchData.name || undefined;

    this.router.navigate([], {
      queryParams: {
        text,
        status,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  trackById(index: number, item: any): number {
    return item.id;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
